.section-program {
  padding-top: 57px;
  padding-bottom: 15px;
}

.section-program__title {
  text-align: center;
  text-transform: uppercase;
  display: block;
  margin-bottom: 51px;
  color: #333333;
}

.section-program__carousel {
  .carousel-indicators {
    position: relative;
    max-width: 534px;
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;

    span {
      position: relative;
      z-index: 2;
    }

    li {
      text-indent: 0;
      width: auto;
      height: auto;
      opacity: 1;
      font-weight: 900;
      font-size: 20px;
      line-height: 25px;
      text-transform: uppercase;
      color: #333333;
      position: relative;

      &.active {
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          width: calc(100% + 30px);
          height: 0;
          border-bottom: 7px solid #BF269E;
          opacity: 0.3;
          transform: translate(-50%, -50%);
          z-index: 1;
        }
      }
    }
  }

  .carousel-control-btn {
    width: 68px;
    height: 68px;
    background: #FFFFFF;
    border: 1px solid #663755;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    top: 41%;
  }

  .carousel-control-next-icon, .carousel-control-prev-icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 11px;
    height: 23px;
    display: block;
    background: url("../img/arrow-right-slider.svg") no-repeat center;
    background-size: 100% auto;
  }

  .carousel-control-prev-icon {
    transform: rotate(180deg) translate(50%, 50%);
  }

  .carousel-item {
    padding-bottom: 30px;
  }
}

@media (max-width: 1199.98px) {
  .section-program__carousel .carousel-item {
    padding-bottom: 100px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .section-program__carousel .carousel-control-btn {
    top: calc(100% - 80px);
  }

  .carousel-control-prev {
    left: 40%;
  }

  .carousel-control-next {
    right: 40%;
  }
}

@media (max-width: 991.98px) {
  .carousel-control-prev {
    left: 37%;
  }

  .carousel-control-next {
    right: 37%;
  }
}

@media (max-width: 767.98px) {
  .section-program__carousel .carousel-indicators li {
    font-size: 16px;
  }

  .section-program__carousel .carousel-indicators {
    padding-left: 25px;
    padding-right: 25px;
  }

  .carousel-control-prev {
    left: auto;
    right: 55%;
  }

  .carousel-control-next {
    right: auto;
    left: 55%;
  }

  .section-program__carousel .carousel-control-btn {
    width: 30px;
    height: 30px;
  }

  .section-program__carousel .carousel-control-next-icon, .section-program__carousel .carousel-control-prev-icon {
    width: 8px;
  }
}

@media (max-width: 575.98px) {
  .section-program__carousel .carousel-indicators {
    flex-direction: column;
    display: inline-flex;
  }

  .section-program__carousel {
    display: flex;
    flex-direction: column;
  }

  .section-program__carousel .carousel-indicators li {
    text-align: center;
  }
}